.App {
  text-align: center;
  font-family: "Comic Sans MS", cursive, sans-serif;
  min-height: 100vh;
}

.App-logo {
  margin: auto;
  animation: App-logo-spin infinite 20s linear;
  height: 16vmin;
  width: 13vmin;
  pointer-events: none;
}

.App-header {
  padding: 2vmin;
  text-shadow: black -3px -1px 0px;
  background-color: yellow;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: calc(20px + 2vmin);
}
.blinkMe {
  animation: blinker 1s linear infinite;
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}
.mediaHolder {
  margin-top: 60px;
  margin-bottom: 60px;
  display: flex;
  padding: 20px;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.audio {
  /* opacity: 0; */
}
.video {
  max-height: 500px;
  max-width: 250px;
  border-left: 3px white solid;
  border-top: 3px white solid;
  border-bottom: 3px white solid;
}
.title {
  margin: 0;
}

.videoHolder {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: stretch;
}

.videoText {
  display: flex;
  flex-direction: column;
  background-color: yellow;
  padding: 10px;
  max-width: 250px;
  max-height: 424px;
  overflow: hidden;
  font-weight: bold;
  border-right: 3px white solid;
  border-top: 3px white solid;
  border-bottom: 3px white solid;
}

.mixHolder {
  font-size: calc(5vmin);
  border: white 3px solid;
  padding: 10px;
  margin: auto;
  max-height: 400px;
  max-width: 400px;
  width: 60vw;
  height: 60vw;
  background-size: cover;
  background-position: center;
  color: white;
  text-shadow: black -3px -1px 2px;
  display: flex;
  align-items: center;
  text-decoration: underline;
}

.mixHolder:hover {
  border: white 3px dotted;
}

.App-link {
  color: white;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
